import { getProductByShopId, shelvesProduct, delProductById, getShopStatusByUserId } from '@/request/API/index.js';
import { mapState } from 'vuex';
export default {
	data() {
		return {
			shopStatus: -1,
			imgURl: '',
			productId: '',
			productList: [], //商品列表
			isShow: false,
			pageNo: 1,
			sortWay: 0 //价格排序方式 0升序 1降序
		};
	},
	onShow() {
		if (this.shopId) {
			this.imgURl = imgURl;
			this.getShopStatusByUserId(this.userId);
			this.productList = [];
			this.pageNo=1;
			this.getProductByShopId(this.pageNo, 10, this.shopId, this.sortWay);
		}
	},
	//上拉加载
	onReachBottom() {
		this.pageNo++;
		this.getProductByShopId(this.pageNo, 10, this.shopId, this.sortWay);
	},
	computed: {
		...mapState(['userId', 'shopId'])
	},
	methods: {
		isShowChange2() {
			this.isShow = false;
		},
		//获取用户入住状态
		getShopStatusByUserId(userId) {
			getShopStatusByUserId(userId).then(res => {
				if (res.data.code == 0) {
					this.shopStatus = res.data.data.shopStatus;
				}
			});
		},
		//获取商品数据
		getProductByShopId(pageNo, pageSize, shopId, sortWay) {
			getProductByShopId(pageNo, pageSize, shopId, sortWay).then(res => {
				if (res.data.code == 0) {
					if (res.data.data.list.length == 0) {
						uni.showToast({
							title: '没有更多数据了',
							icon: 'none',
							duration: 1000
						});
					}
					this.productList = [...this.productList, ...res.data.data.list];
				}
			});
		},
		isShowChange(id) {
			this.productId = id;
			this.isShow = true;
		},
		//编辑
		compile() {
			this.isShow = false;
			uni.navigateTo({
				url: '/pages/addProduct/addProduct?id=' + this.productId
			});
		},
		//删除
		Delete() {
			delProductById(this.productId).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: '删除成功',
						icon: 'none',
						duration: 1000
					});
					this.productList = [];
					this.pageNo=1;
					this.getProductByShopId(this.pageNo, 10, this.shopId, this.sortWay);
				}
			});
			this.isShow = false;
		},
		//上架
		putaway() {
			let publishStatus = 1; //1上架
			shelvesProduct(this.productId, publishStatus).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: '上架成功',
						icon: 'none',
						duration: 1000
					});
					this.productList = [];
						this.pageNo=1;
					this.getProductByShopId(this.pageNo, 10, this.shopId, this.sortWay);
				}
			});
			this.isShow = false;
		},
		//下架
		soldOut() {
			let publishStatus = 0; //0下架
			shelvesProduct(this.productId, publishStatus).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: '下架成功',
						icon: 'none',
						duration: 1000
					});
					this.productList = [];
						this.pageNo=1;
					this.getProductByShopId(this.pageNo, 10, this.shopId, this.sortWay);
				}
			});
			this.isShow = false;
		},
		gotoAdd() {
			if (this.shopStatus == -1) {
				uni.showToast({
					title: '还为入驻商家',
					icon: 'none',
					duration: 1000
				});
				return;
			}
			if (this.shopStatus == 0) {
				uni.showToast({
					title: '信息审核中',
					icon: 'none',
					duration: 1000
				});
				return;
			}
			if (this.shopStatus == 2) {
				uni.showToast({
					title: '商家入驻驳回，请重新入驻',
					icon: 'none',
					duration: 1000
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/addProduct/addProduct'
			});
		}
	}
};
